.strip{
  border: 5px solid transparent;
  border-image: linear-gradient(to left top,#adf2f7 10%,  transparent 30%, transparent 70%, #adf2f7 90%);
  border-image-slice: 1;
  margin-top: 150px;
}

.container{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 600px;
  max-width: 80%;
  margin: 0 auto;
  margin-bottom: 100px;
}
.video{
  position: relative;
  width: 75%;
  padding-top: 43.26%;
  background:  url(../images/free-icon-play-button-3811231.png) no-repeat center ;
  background-size: 10%;
}
.video:hover{
  cursor: pointer;
  background-size: 13%;
}
iframe{
  position: absolute;
  left: 0px;
  right: 0px;
  top: 0px;
  bottom: 0px;
  width: 100%;
  height: 100%;
}

